<template lang="">
  <div >

    <transition-group
      class="icon"
      appear
      tag="div"
      @before-enter = "beforeEnter"
      @enter = "enter"
    >
      <!-- <li v-for="(icon, index) in icons" :key="index">
        <img :src="icon.name">
      </li> -->
      <li v-for="(icon, index) in icons" :key="icon.name" :data-index="index">
        <img :src="icon.name">
      </li>
    </transition-group>

  </div>
</template>

<script>
import { ref } from "vue";
import gsap from 'gsap';

export default {
  components:{
  },
  setup() {

    const icons = ref([
      { name: require('../../assets/icon/c.png'), text: 'c++'},
      { name: require('../../assets/icon/python.png'), text: 'python'},
      { name: require('../../assets/icon/react.png'), text: 'react'},
      { name: require('../../assets/icon/vuejs.png'), text: 'vue'},
    ]);

    const beforeEnter = (el) =>{
      el.style.opacity = 0;
      el.style.transform = 'translateY(100px)'
    }

    const enter = (el,done) =>{
      gsap.to(el, {
        opacity: 1,
        y: 0,
        duration: 0.8,
        onComplete: done,
        delay: el.dataset.index * 0.2  //each object appear one by one with index  in li
      })
    }

    return {
      icons,
      enter,
      beforeEnter,

    };
  },
};
</script>

<style lang="scss" scoped>
.icon {
  @apply flex justify-center gap-6 m-4 list-none;

  img {
    @apply w-12 h-12 m-1;
    transform-origin: center;
    transition: width 0.5s;
    transition: height 0.5s;
  }

  img:hover {
    width: 3.5rem;
    height: 3.5rem;
  }
}

.slide-fade-enter-active {
  transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-leave-to
/* .slide-fade-leave-active for <2.1.8 */ {
  transform: translateY(-10px);
  opacity: 0;
}
.slide-fade-enter-from {
  transform: translateY(10px);
  opacity: 0;
}

</style>
